@use '../../styles/styles' as *;

.header {
    @extend .panel;

    display: flex;
    flex-direction: column;
    align-items: center;

    min-height: 55px;
    position: relative;
    background-color: $header-footer-bg-color;
    z-index: 1;

    > div:first-child {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
    }

    @include viewport("md") {
        flex-direction: row;
        justify-content: space-between;
    }
}

.ghost {
    padding: 0 10px;

    a {
        $size: 50px;
        width: $size;
        height: $size;
        display: inline-block;

        background: {
            image: url('./ghost200x175.png');
            size: $size;
            repeat: no-repeat;
            position: 0 4px;
        }

        &:hover {
            background-image: url('./ghost200x175glow.png');
            transition: background-image 0.5s;
        }

    }
}

.title {
    display: inline-block;
    font-size: 1.5em;
    color: $inactive-text-color;
    text-shadow: 1px 2px 2px black;
    cursor: default;
}

.signInLink {
    @extend .hoverable-text;
    padding: 10px;
}