@use '../../../styles/styles' as *;

.container {
    grid-area: selector;
    display: flex;
    height: fit-content;
    
    /* flex-direction: column; */
    justify-content: space-between;
    padding: 5px 5px 5px 5px;

    @include viewport('sm') {
        flex-direction: row;
        height: fit-content;
    }

    @include viewport('md') {
        flex-direction: column;
        width: 340px;
        height: fit-content;
    }
}


.badge {
    font-family: 'Open Sans', sans-serif;
    position: relative;
    opacity: 0.6;
    padding: 0 10px;
    width: 100%;
    height: 96px;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0 0 5px 0;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0);

    @include viewport('sm') {
        min-width: 150px;
        max-width: 320px;
        width: 29.5%;
    }

    @include viewport('md') {
        width: 100%;
    }

}


.badge:hover,
.badge.active {
    opacity: 1.0;
    /* width: 33%; */
    /* transition: .2s; */
    /* border: 1px solid #111; */
    /*  box-shadow: 0px 0 5px black; */
}

.class {
    position: absolute;
    top: 10px;
    left: 95px;
    color: rgba(240, 240, 240, 0.9);
    font-size: 1.2em;
}

.race {
    position: absolute;
    top: 40px;
    left: 95px;
    color: rgba(200, 200, 200, 0.7);
    font-size: 1em;
}

.light {

    display: none;

    @include viewport('md') {
        display: block;
        /* min-width: 320px;
        width: 320px; */
        font-family: Arial, sans-serif;
        position: absolute;
        top: 5px;
        right: 14px;
        color: rgb(241, 201, 68);
        font-size: 2em;
    }
}

